import { lazy } from "react";
import { Helmet } from "react-helmet";
const Header = lazy(() => import("../ComponentChilds/Header"));
const ContactUs = lazy(() => import("../ComponentChilds/Contact/contactUs"));
const Footer = lazy(() => import("../ComponentChilds/Footer"));

const ContactPage = ({ basketItemCount }) => {
  return (
    <>
      <Helmet>
        <title>تماس با ما | آلمکس اسپورت</title>
        <meta
          name="description"
          content="با آلمکس اسپورت در ارتباط باشید. اطلاعات تماس، آدرس، شماره‌تلفن، ایمیل و راه‌های ارتباطی در شبکه‌های اجتماعی برای مشاوره و پشتیبانی."
        />
        <meta
          name="keywords"
          content="تماس با ما, آلمکس اسپورت, شماره تماس, پشتیبانی, ایمیل, آدرس فروشگاه, مشاوره خرید"
        />
        <meta property="og:title" content="تماس با ما | آلمکس اسپورت" />
        <meta
          property="og:description"
          content="جهت ارتباط با تیم پشتیبانی و دریافت مشاوره خرید با ما تماس بگیرید."
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://allmaxsport.com/contact" />
        <meta property="og:image" content="https://allmaxsport.com/logo.jpg" />
        <link rel="canonical" href="https://allmaxsport.com/contact" />

        {/* Schema.org JSON-LD */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "LocalBusiness",
            name: "آلمکس اسپورت",
            image: "https://allmaxsport.com/logo.jpg",
            url: "https://allmaxsport.com",
            telephone: "+989123456789",
            email: "info@gymstore.com",
            address: {
              "@type": "PostalAddress",
              streetAddress: "خیابان آزادی، پلاک ۴۲",
              addressLocality: "تهران",
              addressCountry: "IR",
            },
            sameAs: [
              "https://instagram.com",
              "https://t.me",
              "https://wa.me/989392566494",
            ],
            description:
              "تماس با آلمکس اسپورت برای مشاوره خرید، پشتیبانی و اطلاعات بیشتر درباره محصولات ورزشی.",
          })}
        </script>
      </Helmet>
      <Header basketItemCount={basketItemCount} />
      <main>
        <ContactUs />
      </main>
      <Footer />
    </>
  );
};

export default ContactPage;
